var _this;
export default {
	data() {
		return {
			userInfo: Object,
			baseUrl: "http://39.105.48.243:8080/crlink/",
			userName: '',
			userRealName: '',
			picStr: ''
		}
	},
	onLoad() {
		_this = this;
		this.getuserInfo()
	},
	onShow() {
		this.getuserInfo()
	},
	methods: {
		getuserInfo() {
			var that = this;
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('user!ajaxGetUserInfo.action', {
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {
					that.userInfo = res.inf
					that.userName = that.userInfo.nickName
					that.userRealName = that.userInfo.realName
				} else {
					uni.showModal({
						title: res.res.error
					})
				}
			})
		},

		telClcik() {
			// uni.navigateTo({
			// 	url:'./changePhoneNumber'
			// })
			uni.navigateTo({
				url: './changePhoneNumber'
			})
		},
		changePic() {
			// 用户点击了从图库上传
			console.log("用户点击了从图库上传");
			uni.chooseImage({
				count: 1,
				sizeType: ["compressed"],
				success(response) {



					// 选择图片后, 返回的数据
					var fileUrl = response.tempFilePaths[0]
					_this.picStr = fileUrl
					console.log("用户点击了从图库上传222 =" + fileUrl);
				}
			})
		},

		upNewUserMsg() {
			console.log("111111");
			var loginkey = uni.getStorageSync('loginKey');
			uni.uploadFile({
					url: this.baseUrl + 'app/user!ajaxUpdateUser.action', //仅为示例，非真实的接口地址
					filePath: _this.picStr?_this.picStr:'',
					name: 'imgFile',
					formData: {
						'loginKey': loginkey,
						'realName': this.userRealName,
						'sex': "1",
						'nickName': this.userName,
					},
					success: (uploadFileRes) => {
						uni.showToast({
							title:"更改成功"
						})
					}
			
				})
		}
	},
	computed: {

	}
}
